@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap);

.movie {
    background-color: #ffffff;
    width: 600px;
    padding: 20px;
    box-sizing: border-box;
    border: 1px solid #eeeeee;
    margin: 100px;
    position: relative;
    border-radius: 20px;
    box-shadow: 0px 0px 30px #e0e0e0;
}

.movie img {
    width: 150px;
    position: absolute;
    top: -50px;
    left: 20px;
    box-shadow: 0px 0px 20px #333333;
    border-radius: 20px;
}

.movie_data {
    width: 60%;
    margin-left: 200px;
    padding: 10px;
    box-sizing: border-box;
}

.movie_title {
    font-size: 24px;
    font-weight: 400;
}

.movie_year {
    font-size: 16px;
    font-weight: 400;
    color: #333333;
}

.genres {
    float: left;
    font-size: 16px;
    font-weight: 100;
    color: #999999;
}

.genres_genres {
    margin-right: 10px;
    float: left;
}

.movie_summary {
    margin-top: 20px;
    float: left;
    font-size: 16px;
    font-weight: 200;
    color: #555555;
}

*{margin: 0; padding: 0;}

ul, li {
    list-style: none;
}

body {
    font-family: 'Noto Sans KR', sans-serif;
}

section {
    width: 100%;
    height: 100%;
    background-color: #eeeeee;
}

.loader {
    width: 100%;
    height: 100vh;
    background-color: #1d1d1d;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 24px;
    font-weight: 500;
}

.movies {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
